const Voices = () => {
  const [selectedProvider, setSelectedProvider] = useState<string>("");
  const [selectedVoice, setSelectedVoice] = useState<string>("");
  const [testText, setTestText] = useState("Hello, this is a test of the voice synthesis.");
  const [speed, setSpeed] = useState(1.0);
  const [pitch, setPitch] = useState(1.0);
  const [isTestDialogOpen, setIsTestDialogOpen] = useState(false);
  const [isConfigDialogOpen, setIsConfigDialogOpen] = useState(false);
  const [isCloneDialogOpen, setIsCloneDialogOpen] = useState(false);
  const [cloneFiles, setCloneFiles] = useState<File[]>([]);
  const [cloneName, setCloneName] = useState("");
  const [cloneDescription, setCloneDescription] = useState("");

  const queryClient = useQueryClient();

  // Fetch available voices
  const { data: voicesData, isLoading: voicesLoading, error: voicesError } = useQuery({
    queryKey: ["voices", "available"],
    queryFn: getAvailableVoices,
    retry: 1,
  });

  // Fetch voice statistics
  const { data: statsData, isLoading: statsLoading } = useQuery({
    queryKey: ["voices", "stats"],
    queryFn: getVoiceStats,
    retry: 1,
  });

  // Test voice mutation
  const testVoiceMutation = useMutation({
    mutationFn: testVoiceSynthesis,
    onSuccess: (data) => {
      toast.success("Voice test completed successfully");
      console.log("Test result:", data);
    },
    onError: (error: any) => {
      console.error("Voice test failed:", error);
      toast.error(error.response?.data?.message || "Voice test failed");
    },
  });

  // Create voice config mutation
  const createConfigMutation = useMutation({
    mutationFn: createVoiceConfig,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["voices"] });
      setIsConfigDialogOpen(false);
      toast.success("Voice configuration created successfully");
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to create voice configuration");
    },
  });

  // Clone voice mutation
  const cloneVoiceMutation = useMutation({
    mutationFn: cloneVoice,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["voices"] });
      setIsCloneDialogOpen(false);
      setCloneFiles([]);
      setCloneName("");
      setCloneDescription("");
      toast.success("Voice cloned successfully");
    },
    onError: (error: any) => {
      toast.error(error.response?.data?.message || "Failed to clone voice");
    },
  });

  const handleTestVoice = () => {
    if (!selectedProvider || !selectedVoice) {
      toast.error("Please select a provider and voice");
      return;
    }

    testVoiceMutation.mutate({
      provider: selectedProvider,
      voiceId: selectedVoice,
      text: testText,
      speed,
      pitch,
    });
  };

  const handleCreateConfig = (data: VoiceConfig) => {
    createConfigMutation.mutate(data);
  };

  const handleCloneVoice = () => {
    if (!selectedProvider || !cloneName || cloneFiles.length === 0) {
      toast.error("Please fill in all required fields and upload audio files");
      return;
    }

    cloneVoiceMutation.mutate({
      provider: selectedProvider,
      name: cloneName,
      description: cloneDescription,
      files: cloneFiles,
    });
  };

  const handleFileUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(e.target.files || []);
    setCloneFiles(files);
  };

  const providers = voicesData?.data || {};

  if (voicesError) {
    return (
      <Page>
        <div className="space-y-6">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">Voice Synthesis</h1>
            <p className="text-muted-foreground">
              Manage and configure text-to-speech voices from different providers
            </p>
          </div>
          <Card>
            <CardContent className="pt-6">
              <div className="text-center">
                <p className="text-muted-foreground mb-4">
                  Unable to connect to the backend API. Please ensure the backend server is running.
                </p>
                <Button onClick={() => window.location.reload()}>
                  Retry Connection
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      </Page>
    );
  }

  return (
    <Page>
      <div className="space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">Voice Synthesis</h1>
            <p className="text-muted-foreground">
              Manage and configure text-to-speech voices from different providers
            </p>
          </div>
        <div className="flex gap-2">
         

        

          {/* <Dialog open={isConfigDialogOpen} onOpenChange={setIsConfigDialogOpen}>
            <DialogTrigger asChild>
              <Button>
                <Volume2 className="h-4 w-4 mr-2" />
                Create Config
              </Button>
            </DialogTrigger>
            <DialogContent>
              <DialogHeader>
                <DialogTitle>Create Voice Configuration</DialogTitle>
                <DialogDescription>
                  Create a new voice configuration for your assistants
                </DialogDescription>
              </DialogHeader>
              <div className="space-y-4">
                <p className="text-sm text-muted-foreground">
                  Voice configuration form will be implemented here.
                </p>
              </div>
            </DialogContent>
          </Dialog> */}
        </div>
      </div>

      <Tabs defaultValue="providers" className="space-y-4">
        <TabsList>
          <TabsTrigger value="providers">Available Voices</TabsTrigger>
          <TabsTrigger value="stats">Statistics</TabsTrigger>
        </TabsList>

        <TabsContent value="providers" className="space-y-4">
          {voicesLoading ? (
            <div className="flex items-center justify-center h-64">
              <Loader2 className="h-8 w-8 animate-spin" />
            </div>
          ) : (
            <div className="grid gap-6">
              {Object.keys(providers).length === 0 ? (
                <Card>
                  <CardContent className="pt-6">
                    <div className="text-center">
                      <p className="text-muted-foreground">
                        No voices available. Please check your backend configuration.
                      </p>
                    </div>
                  </CardContent>
                </Card>
              ) : (
                Object.entries(providers).map(([providerName, voices]: [string, any]) => (
                  <Card key={providerName}>
                    <CardHeader>
                      <CardTitle className="flex items-center gap-2">
                        <Volume2 className="h-5 w-5" />
                        {providerName}
                      </CardTitle>
                      <CardDescription>
                        {Array.isArray(voices) ? voices.length : 0} voices available
                      </CardDescription>
                    </CardHeader>
                    <CardContent>
                      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                        {Array.isArray(voices) && voices.map((voice: any) => (
                          <Card key={voice.id || voice.name} className="p-4">
                            <div className="space-y-2">
                              <div className="flex items-center justify-between">
                                <h4 className="font-semibold">{voice.name}</h4>
                                <div className="flex gap-1">
                                  {voice.gender && (
                                    <Badge variant="secondary">{voice.gender}</Badge>
                                  )}
                                  {voice.accent && (
                                    <Badge variant="outline">{voice.accent}</Badge>
                                  )}
                                </div>
                              </div>
                              {voice.description && (
                                <p className="text-sm text-muted-foreground">
                                  {voice.description}
                                </p>
                              )}
                              {voice.language && (
                                <div className="text-xs text-muted-foreground">
                                  Language: {voice.language}
                                </div>
                              )}
                              {voice.previewUrl && (
                                <VoicePreviewButton
                                  previewUrl={voice.previewUrl}
                                  voiceName={voice.name}
                                />
                              )}
                            </div>
                          </Card>
                        ))}
                      </div>
                    </CardContent>
                  </Card>
                ))
              )}
            </div>
          )}
        </TabsContent>

        <TabsContent value="stats" className="space-y-4">
          {statsLoading ? (
            <div className="flex items-center justify-center h-64">
              <Loader2 className="h-8 w-8 animate-spin" />
            </div>
          ) : (
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Total Synthesis</CardTitle>
                  <BarChart3 className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">
                    {statsData?.data?.totalSynthesis?.toLocaleString() || 0}
                  </div>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Total Characters</CardTitle>
                  <BarChart3 className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">
                    {statsData?.data?.totalCharacters?.toLocaleString() || 0}
                  </div>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Avg Latency</CardTitle>
                  <BarChart3 className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">
                    {statsData?.data?.averageLatency || 0}ms
                  </div>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Error Rate</CardTitle>
                  <BarChart3 className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">
                    {((statsData?.data?.errorRate || 0) * 100).toFixed(1)}%
                  </div>
                </CardContent>
              </Card>
            </div>
          )}
        </TabsContent>
      </Tabs>
      </div>
    </Page>
  );
};

// Voice Preview Button Component
const VoicePreviewButton = ({ previewUrl, voiceName }: { previewUrl: string; voiceName: string }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [audio, setAudio] = useState<HTMLAudioElement | null>(null);
  const [hasError, setHasError] = useState(false);

  // Cleanup function
  const cleanup = () => {
    if (audio) {
      audio.pause();
      audio.removeEventListener('loadstart', handleLoadStart);
      audio.removeEventListener('canplay', handleCanPlay);
      audio.removeEventListener('ended', handleEnded);
      audio.removeEventListener('error', handleError);
      audio.removeEventListener('loadeddata', handleLoadedData);
      setAudio(null);
    }
  };

  // Event handlers
  const handleLoadStart = () => setIsLoading(true);
  const handleCanPlay = () => setIsLoading(false);
  const handleLoadedData = () => setIsLoading(false);
  const handleEnded = () => {
    setIsPlaying(false);
    setIsLoading(false);
  };
  const handleError = (e: Event) => {
    console.error('Audio error for', voiceName, ':', e);
    setIsLoading(false);
    setIsPlaying(false);
    setHasError(true);
    toast.error(`Preview not available for ${voiceName}`);
  };

  const handlePlayPause = async () => {
    if (isPlaying && audio) {
      audio.pause();
      setIsPlaying(false);
      return;
    }

    // Reset error state
    setHasError(false);

    try {
      setIsLoading(true);

      // Cleanup previous audio
      cleanup();

      // Validate URL
      if (!previewUrl || previewUrl.trim() === '') {
        throw new Error('No preview URL available');
      }

      // Check if this is a dynamic preview URL (starts with /api/voices/preview)
      if (previewUrl.startsWith('/api/voices/preview')) {
        // Handle dynamic preview generation
        await handleDynamicPreview(previewUrl);
      } else {
        // Handle static preview URLs
        await handleStaticPreview(previewUrl);
      }

    } catch (error) {
      console.error('Error playing voice preview:', error);
      setIsLoading(false);
      setIsPlaying(false);
      setHasError(true);

      // More specific error messages
      if (error instanceof Error) {
        if (error.message.includes('timeout')) {
          toast.error(`Preview timeout for ${voiceName}`);
        } else if (error.message.includes('No preview URL')) {
          toast.error(`No preview available for ${voiceName}`);
        } else if (error.message.includes('Voice preview not available')) {
          toast.error(`${voiceName} preview is currently unavailable`);
        } else {
          toast.error(`Preview unavailable for ${voiceName}`);
        }
      } else {
        toast.error(`Failed to play preview for ${voiceName}`);
      }
    }
  };

  // Handle dynamic preview generation
  const handleDynamicPreview = async (previewUrl: string) => {
    try {
      console.log('ðŸ”„ Making request to:', previewUrl);

      // Use axiosInstance which already has proper authentication setup
      const response = await axiosInstance.get(previewUrl);

      console.log('ðŸŽµ Voice preview response received:', response.data);
      console.log('ðŸ” Response analysis:');
      console.log('  - success:', response.data.success);
      console.log('  - audioUrl:', response.data.audioUrl);
      console.log('  - note:', response.data.note);
      console.log('  - message:', response.data.message);

      if (response.data.success && response.data.audioUrl) {
        // Play the generated audio
        console.log('âœ… Path 1: Playing audio from:', response.data.audioUrl);
        await handleStaticPreview(response.data.audioUrl);
        toast.success(`Playing ${voiceName} preview`);
      } else if (response.data.note) {
        // Show informative message about preview status (don't throw error)
        console.log('âœ… Path 2: Showing note toast:', response.data.note);
        toast.info(response.data.note);
        setIsLoading(false);
        setHasError(false);
        return; // Exit gracefully without throwing error
      } else if (response.data.message && !response.data.audioUrl) {
        // Show backend message when no audio URL
        console.log('âœ… Path 3: Showing message toast:', response.data.message);
        toast.info(response.data.message);
        setIsLoading(false);
        setHasError(false);
        return; // Exit gracefully without throwing error
      } else {
        // Fallback error
        console.log('âŒ Path 4: No valid response found');
        console.log('âŒ Full response:', JSON.stringify(response.data, null, 2));
        throw new Error('Voice preview not available');
      }
    } catch (error: any) {
      console.error('âŒ Error in handleDynamicPreview:', error);
      if (error.response?.status === 401) {
        throw new Error('Authentication failed - please log in again');
      }
      throw error;
    }
  };

  // Handle static preview URLs
  const handleStaticPreview = async (audioUrl: string) => {
    const newAudio = new Audio();

    // Set up event listeners before setting src
    newAudio.addEventListener('loadstart', handleLoadStart);
    newAudio.addEventListener('canplay', handleCanPlay);
    newAudio.addEventListener('loadeddata', handleLoadedData);
    newAudio.addEventListener('ended', handleEnded);
    newAudio.addEventListener('error', handleError);

    // Configure audio settings
    newAudio.preload = 'metadata';
    newAudio.crossOrigin = 'anonymous';

    setAudio(newAudio);

    // Set source and load
    newAudio.src = audioUrl;
    newAudio.load();

    // Wait for audio to be ready
    await new Promise((resolve, reject) => {
      const timeout = setTimeout(() => {
        reject(new Error('Audio load timeout'));
      }, 10000); // 10 second timeout

      newAudio.addEventListener('canplay', () => {
        clearTimeout(timeout);
        resolve(void 0);
      }, { once: true });

      newAudio.addEventListener('error', () => {
        clearTimeout(timeout);
        reject(new Error('Audio load failed'));
      }, { once: true });
    });

    // Play the audio
    await newAudio.play();
    setIsPlaying(true);
    setIsLoading(false);
  };

  // Cleanup on unmount
  useEffect(() => {
    return cleanup;
  }, []);

  return (
    <Button
      variant="outline"
      size="sm"
      className="w-full"
      onClick={handlePlayPause}
      disabled={isLoading || hasError}
    >
      {isLoading ? (
        <Loader2 className="h-3 w-3 mr-1 animate-spin" />
      ) : isPlaying ? (
        <Pause className="h-3 w-3 mr-1" />
      ) : hasError ? (
        <Volume2 className="h-3 w-3 mr-1 opacity-50" />
      ) : (
        <Play className="h-3 w-3 mr-1" />
      )}
      {isLoading ? 'Loading...' : isPlaying ? 'Pause' : hasError ? 'Unavailable' : 'Preview'}
    </Button>
  );
};

export default Voices;
